/************
4. 1199px
************/

@media only screen and (max-width: 1199px) {
  .home_slider_prev {
    left: 25px;
  }
  .home_slider_next {
    right: 25px;
  }
  .about_col_left {
    padding-right: 15px;
  }
  .about_col_right {
    padding-left: 15px;
  }
  .about_col_middle {
    padding-left: 15px;
    padding-right: 15px;
  }
  .milestone:not(:last-child)::after {
    display: none;
  }
  .team_image {
    width: 160px;
    height: 140px;
  }

  /* courses page */
  .sidebar {
    padding-left: 20px;
  }
  .gallery_item {
    width: calc((100% - 24px) / 3);
    margin-bottom: 12px;
  }
}

