/************
6. 991px
************/

@media only screen and (max-width: 991px) {
  .main_nav,
  .search_button {
    display: none;
  }
  .top_bar_contact_list li i,
  .top_bar_contact_list li > div,
  .login_button a {
    font-size: 12px;
  }
  .login_button {
    width: 140px;
  }
  .hamburger {
    display: inline-block;
    margin-left: 33px;
  }
  .home_slider_form_container {
    width: 100%;
  }
  .home_search_button {
    margin-top: 15px;
  }

  .about_col:not(:last-child) {
    margin-bottom: 40px;
  }
  .feature_col:not(:last-child) {
    margin-bottom: 30px;
  }
  .course_col:not(:last-child) {
    margin-bottom: 40px;
  }
  .counter_form {
    position: relative;
    top: 0;
    right: auto;
    width: 100%;
    height: auto;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .counter_content {
    padding-top: 100px;
    padding-bottom: 68px;
  }
  .counter {
    padding-bottom: 100px;
  }
  .event_col:not(:last-child) {
    margin-bottom: 20px;
  }
  .team_col .team_item {
    padding-left: 0px;
    padding-right: 0px;
  }
  .team_col:first-child .team_item {
    padding-left: 0;
    padding-right: 0px;
  }
  .team_col:nth-child(4n) .team_item {
    padding-left: 0px;
    padding-right: 0;
  }
  .team_col:nth-child(odd) .team_item {
    padding-right: 6px;
  }
  .team_col:nth-child(even) .team_item {
    padding-left: 6px;
  }

  .newsletter_container {
    height: auto;
    padding-top: 40px;
    padding-bottom: 40px;
  }
  .newsletter_form_container {
    margin-top: 19px;
  }
  .footer_col:not(:last-child) {
    margin-bottom: 60px;
  }
  .footer_contact,
  .footer_links {
    padding-left: 0;
  }
  .footer_links ul {
    columns: 1;
    -webkit-columns: 1;
    -moz-columns: 1;
  }
  .footer_mobile {
    float: none;
  }
  .footer_logo_container {
    margin-top: 0;
  }
  .copyright {
    height: auto;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .cr_list {
    margin-top: 30px;
  }
  .cr_list li:not(:last-child) {
    margin-right: 20px;
  }
  .copyright div,
  .cr_list li a {
    font-size: 13px;
  }
}

